package ovh.devcraft.webrtc.data.filetransfer

import kotlinx.coroutines.await
import org.khronos.webgl.Uint8Array
import ovh.devcraft.webrtc.fileio.util.toUint8Array
import kotlin.js.Promise

/**
 * Specialized writer for Web Worker that reports progress.
 */
class WorkerFileStreamWriter(
    private val writable: FileSystemWritableFileStream,
    private val onProgress: (Long) -> Unit
) {
    private var totalBytesWritten = 0L

    suspend fun writeChunk(data: ByteArray) {
        // Write directly to the stream. Browser handles internal buffering.
        writable.write(data.toUint8Array().unsafeCast<Uint8Array>()).await()
        totalBytesWritten += data.size
        onProgress(totalBytesWritten)
    }

    suspend fun close() {
        writable.close().await()
    }
}

// External declarations for File System Access API
external class FileSystemWritableFileStream {
    fun write(data: Any?): Promise<Unit>
    fun close(): Promise<Unit>
}

external class FileSystemFileHandle {
    fun createWritable(): Promise<FileSystemWritableFileStream>
}