package ovh.devcraft.webrtc.data.filetransfer

import kotlinx.coroutines.*
import kotlinx.coroutines.channels.Channel
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json
import org.khronos.webgl.ArrayBuffer
import org.w3c.dom.DedicatedWorkerGlobalScope
import org.w3c.dom.MessageEvent
import ovh.devcraft.webrtc.fileio.util.toByteArray
import ovh.devcraft.webrtc.fileio.worker.WorkerRequest
import ovh.devcraft.webrtc.fileio.worker.WorkerResponse

private external val self: DedicatedWorkerGlobalScope

private val scope = CoroutineScope(Dispatchers.Default + SupervisorJob())
private var writer: WorkerFileStreamWriter? = null
private val chunkChannel = Channel<ByteArray>(capacity = Channel.UNLIMITED)

fun main() {
    println("FileTransfer Worker: Initializing...")
    
    // Process chunks sequentially
    scope.launch {
        while (true) {
            val chunk = chunkChannel.receive()
            try {
                writer?.writeChunk(chunk)
            } catch (e: Exception) {
                sendResponse(WorkerResponse.Error("Failed to write chunk: ${e.message}"))
            }
        }
    }

    self.onmessage = { event: MessageEvent ->
        val data = event.data
        if (data is ArrayBuffer) {
            val byteArray = data.toByteArray()
            chunkChannel.trySend(byteArray)
        } else {
            // In JS, data is a plain object. We can check properties dynamically.
            val d = data.asDynamic()
            val requestJson = d.request as? String
            
            if (requestJson != null) {
                try {
                    val request = Json.decodeFromString<WorkerRequest>(requestJson)
                    val handle = d.handle.unsafeCast<FileSystemFileHandle?>()
                    scope.launch {
                        handleRequest(request, handle)
                    }
                } catch (e: Exception) {
                    sendResponse(WorkerResponse.Error("Failed to decode request: ${e.message}"))
                }
            } else {
                sendResponse(WorkerResponse.Log("Received unknown message format"))
            }
        }
    }
}

private suspend fun handleRequest(request: WorkerRequest, handle: FileSystemFileHandle?) {
    when (request) {
        is WorkerRequest.Start -> {
            try {
                if (handle == null) {
                    sendResponse(WorkerResponse.Error("No FileSystemFileHandle provided in message"))
                    return
                }
                val writable = handle.createWritable().await()
                writer = WorkerFileStreamWriter(writable) { bytesWritten ->
                    sendResponse(WorkerResponse.Progress(bytesWritten))
                }
                sendResponse(WorkerResponse.Started)
            } catch (e: Exception) {
                sendResponse(WorkerResponse.Error("Failed to start writer: ${e.message}"))
            }
        }
        is WorkerRequest.Close -> {
            try {
                // Ensure all pending chunks are written before closing
                while (!chunkChannel.isEmpty) {
                    yield()
                }
                writer?.close()
                writer = null
                sendResponse(WorkerResponse.Closed)
            } catch (e: Exception) {
                sendResponse(WorkerResponse.Error("Failed to close writer: ${e.message}"))
            }
        }
    }
}

private fun sendResponse(response: WorkerResponse) {
    val json = Json.encodeToString(response)
    self.postMessage(json)
}