package ovh.devcraft.webrtc.fileio.util

import org.khronos.webgl.ArrayBuffer
import org.khronos.webgl.Int8Array
import org.khronos.webgl.Uint8Array

actual fun ByteArray.toUint8Array(): Any {
    val i8 = this.unsafeCast<Int8Array>()
    return Uint8Array(i8.buffer, i8.byteOffset, i8.length)
}

actual fun Any.toByteArray(): ByteArray {
    return when (this) {
        is Int8Array -> this.unsafeCast<ByteArray>()
        is Uint8Array -> Int8Array(this.buffer, this.byteOffset, this.length).unsafeCast<ByteArray>()
        is ArrayBuffer -> Int8Array(this, 0, this.byteLength).unsafeCast<ByteArray>()
        else -> throw IllegalArgumentException("Unsupported type for toByteArray: ${this::class.js.name}")
    }
}

actual fun ByteArray.toArrayBuffer(): Any = (this.toUint8Array().unsafeCast<Uint8Array>()).buffer
