package ovh.devcraft.webrtc.fileio.worker

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
data class FileMetadataDto(
    val fileName: String,
    val fileSize: Long,
    val fileType: String
)

@Serializable
sealed class WorkerRequest {
    @Serializable
    @SerialName("Start")
    data class Start(val metadata: FileMetadataDto) : WorkerRequest()

    @Serializable
    @SerialName("Close")
    data class Close(val dummy: Int = 0) : WorkerRequest()
}

@Serializable
sealed class WorkerResponse {
    @Serializable
    @SerialName("Started")
    data object Started : WorkerResponse()

    /**
     * Reported after each successful write.
     * @param bytesWritten Total bytes written to disk so far.
     */
    @Serializable
    @SerialName("Progress")
    data class Progress(val bytesWritten: Long) : WorkerResponse()

    @Serializable
    @SerialName("Closed")
    data object Closed : WorkerResponse()

    @Serializable
    @SerialName("Error")
    data class Error(val message: String) : WorkerResponse()

    @Serializable
    @SerialName("Log")
    data class Log(val message: String) : WorkerResponse()
}
